"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { ArrowLeft } from "lucide-react"
import Link from "next/link"

// Mock data - in real app this would come from a database
const mockComponents = {
  "engine": [
    {
      id: "ecu_main",
      name: "Main Engine ECU",
      location: "Behind dashboard, driver's side",
      access: "Remove lower dashboard panel",
      image: "/components/ecu_main.png"
    },
    {
      id: "maf_sensor",
      name: "Mass Air Flow Sensor",
      location: "Air intake assembly, after air filter",
      access: "Direct access, unplug connector",
      image: "/components/maf.png"
    }
  ],
  "transmission": [
    {
      id: "tcm",
      name: "Transmission Control Module",
      location: "Behind cabin, near transmission",
      access: "Remove access panel under sleeping compartment",
      image: "/components/tcm.png"
    }
  ],
  "brakes": [
    {
      id: "abs_module",
      name: "ABS Control Module",
      location: "Frame rail, driver's side",
      access: "Visible from under vehicle",
      image: "/components/abs.png"
    }
  ]
}

export default function ComponentLocator() {
  const [searchTerm, setSearchTerm] = useState("")
  const [activeTab, setActiveTab] = useState("engine")

  const filteredComponents = mockComponents[activeTab as keyof typeof mockComponents].filter(
    component => component.name.toLowerCase().includes(searchTerm.toLowerCase())
  )

  return (
    <main className="min-h-screen p-6 md:p-12 bg-zinc-950">
      <div className="max-w-4xl mx-auto">
        <div className="mb-8">
          <Link href="/">
            <Button variant="ghost" className="text-zinc-400 hover:text-white">
              <ArrowLeft className="mr-2 h-4 w-4" />
              Back
            </Button>
          </Link>
        </div>

        <h1 className="text-3xl font-bold text-white mb-8">Component Locator</h1>

        <div className="mb-8">
          <Input
            placeholder="Search components..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="bg-zinc-900 border-zinc-800 text-white"
          />
        </div>

        <Tabs defaultValue="engine" className="mb-8" onValueChange={setActiveTab}>
          <TabsList className="bg-zinc-900 border-zinc-800">
            <TabsTrigger value="engine" className="data-[state=active]:bg-yellow-500 data-[state=active]:text-black">
              Engine
            </TabsTrigger>
            <TabsTrigger value="transmission" className="data-[state=active]:bg-yellow-500 data-[state=active]:text-black">
              Transmission
            </TabsTrigger>
            <TabsTrigger value="brakes" className="data-[state=active]:bg-yellow-500 data-[state=active]:text-black">
              Brakes
            </TabsTrigger>
          </TabsList>

          {Object.keys(mockComponents).map((system) => (
            <TabsContent key={system} value={system}>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {filteredComponents.map((component) => (
                  <Card key={component.id} className="p-6 bg-zinc-900 border-zinc-800">
                    <div className="aspect-video bg-zinc-800 rounded-lg mb-4">
                      {/* In a real app, this would be an actual image */}
                      <div className="w-full h-full flex items-center justify-center text-zinc-600">
                        Component Image
                      </div>
                    </div>
                    <h3 className="text-xl font-semibold text-yellow-500 mb-2">
                      {component.name}
                    </h3>
                    <div className="space-y-4">
                      <div>
                        <h4 className="text-sm font-medium text-zinc-400">Location</h4>
                        <p className="text-white">{component.location}</p>
                      </div>
                      <div>
                        <h4 className="text-sm font-medium text-zinc-400">Access Instructions</h4>
                        <p className="text-white">{component.access}</p>
                      </div>
                    </div>
                  </Card>
                ))}
              </div>
            </TabsContent>
          ))}
        </Tabs>
      </div>
    </main>
  )
}
